import React, {useState} from 'react';
import {View, Text, ScrollView} from 'react-native';
import styles from './styles';

type Props = {
  tableHeaders: string[];
  tableData: any[];
  stickyColumnLeftIndex?: number;
  stickyColumnRightIndex?: number;
  stickyLeft?: boolean;
  stickyRight?: boolean;
  isPaging?: boolean;
};

const TableCM = ({
  tableHeaders,
  tableData,
  stickyColumnLeftIndex = 0,
  stickyColumnRightIndex,
  stickyLeft,
  stickyRight,
  isPaging = true,
}: Props) => {
  return (
    <View style={styles.container}>
      <View style={styles.tableBody}>
        {/* Sticky Name Column */}
        {stickyLeft &&
          stickyColumnLeftIndex &&
          tableHeaders
            .slice(-stickyColumnLeftIndex)
            .reverse()
            .map((header, index) => (
              <View
                key={header}
                style={[
                  styles.stickyColumnScroll,
                  {
                    right: index * 100,
                  },
                  1 === stickyColumnLeftIndex && styles.box,
                ]}>
                {/* Sticky Header Column */}
                <View
                  style={[
                    styles.stickyHeader,
                    header === 'action' && {alignItems: 'center'},
                  ]}>
                  <Text style={styles.headerText}>{header}</Text>
                </View>
                {/* Sticky Data Column */}
                {tableData?.map((rowData, index) => (
                  <View
                    key={index}
                    style={[
                      styles.stickyColumn,
                      header === 'action' && {alignItems: 'center'},
                    ]}>
                    {typeof (rowData as any)[header] !== 'object' ? (
                      <Text style={styles.cellText}>
                        {(rowData as any)[header]}
                      </Text>
                    ) : (
                      (rowData as any)[header]
                    )}
                  </View>
                ))}
              </View>
            ))}

        {/* Scrollable Header + Columns */}
        <ScrollView horizontal>
          {/* Scrollable Columns */}
          <View
            style={[
              styles.row,
              !stickyLeft && {
                marginRight: 0,
              },
            ]}>
            {tableHeaders
              .slice(0, tableHeaders.length - stickyColumnLeftIndex)
              .map((header, index) => (
                <View key={index}>
                  <View
                    style={[
                      styles.stickyHeader,
                      header === 'action' && {alignItems: 'center'},
                    ]}>
                    <Text style={styles.headerText}>{header}</Text>
                  </View>

                  {tableData?.map((rowData, index) => (
                    <View
                      key={index}
                      style={[
                        styles.stickyColumn,
                        header === 'action' && {alignItems: 'center'},
                      ]}>
                      {typeof (rowData as any)[header] !== 'object' ? (
                        <Text style={styles.cellText}>
                          {(rowData as any)[header]}
                        </Text>
                      ) : (
                        (rowData as any)[header]
                      )}
                    </View>
                  ))}
                </View>
              ))}
          </View>
        </ScrollView>
      </View>
    </View>
  );
};

export default TableCM;
